import {h} from 'vue'
// 公告业务模板
import tpla from './tpla.vue';
import tplb from './tplb.vue';
import tplc from './tplc.vue';
// eslint-disable-next-line import/prefer-default-export
export default {
    functional: true,
    props: {
      tplNum: {
        type: String,
        default:'tpla'
      },
      objtext:{
        type: Object,
        default:'swhswh'
      }
    },
    
    render() {
        const obj = {
            tpla,
            tplb,
            tplc
        }
    return h(obj[this.tplNum],{
        ...this.objtext
    });//h1和a标签放到div中
    }
  };
  