<template>
  <div class="t2">
    <h1>{{ tit }}</h1>
    <h1 @click="zfn">{{ text }}</h1>
  </div>
</template>

<script>
export default {
  props: {
    tit: {
      type: String,
      default: "swhswh",
    },
    text: {
      type: String,
      default: "swhswh",
    },
  },
  methods: {
    zfn() {
      this.$emit("fn", "aaaa");
    },
  },
};
</script>

<style>
.t2 h1 {
  color: rgb(148, 12, 0);
}
</style>
