<template>
  <p v-for="(item, index) in tabs" :key="item.id" @click="tabfn(index)">
    {{ item.name }}
  </p>
  <Tpl :tplNum="com" :objtext="objtext" @fn="fn"></Tpl>
</template>

<script>
import Tpl from "./renderTpl/tpl.js";

export default {
  components: { Tpl },
  data() {
    return {
      com: "tplb",
      objtext: {
        tit: "标题001",
        text: "内哦那个内哦那个内哦那个内哦那个",
      },
      tabs: [
        {
          id: 1,
          name: "模板1",
          com: "tpla",
          text: "模板1111111111111111111111",
        },
        {
          id: 2,
          name: "模板2",
          com: "tplb",
          text: "模板222222222222222",
        },
        {
          id: 3,
          name: "模板3",
          com: "tplc",
          text: "模板333333333333333333",
        },
      ],
    };
  },
  methods: {
    tabfn(index) {
      this.com = this.tabs[index].com;
      this.objtext.tit = this.tabs[index].name;
      this.objtext.text = this.tabs[index].text;
    },
    fn(v) {
      console.log(v);
    },
  },
};
</script>

<style>
p {
  cursor: pointer;
}
</style>
